<template>
  <div>
    <Input v-model='inputValue' @on-blur='handlerBlur' :class='{"error-type":isError}' @on-focus='handlerFocus'></Input>
  </div>
</template>
<script>
export default {
  props: {
    value: Number
  },
  data() {
    return {
      inputValue: '',
     
    }
  },
  methods: {
    handlerBlur() {
      let val = Number(this.inputValue);
      if (!this.inputValue) {
        this.handlerError('价格/数量不能为空')
      } else if (val !== val) {
        this.handlerError('价格/数量必须为数字')
      } else {
        this.$emit('change', val)
      }
    },
    format() {

    },
    handlerError(text) {
      this.$Message.error(text)
    },
    handlerFocus() {
      //this.isError = false
    },
  },
  created() {
    this.inputValue = '' + this.value
  },
  watch: {
    value(newVal) {
      this.inputValue = '' + this.value
      console.log(newVal)
    },

  },
  computed: {
    isError() {
      let val = Number(this.inputValue);
      if (!this.inputValue||val !== val) {
        return true
      } else{
        return false
      }
    },
  }
}

</script>
<style>
.error-type .ivu-input {
  border: 1px solid red;
}

</style>



