import NumInput from '../sku/number-input.vue'
import singleUploader from './SingleImgUploader.vue'
import multiUploader from './MultiImgUploader.vue'
import itemSelectModal from './ItemSelectModal.vue'
import multiItemSelectModal from './MultiItemSelectModal.vue'
const Common = {
  install: function(Vue) {
    //注册全局的confirm组件（用来确认删除/操作的）
    Vue.prototype.$Confirm = function(text, title) {
      let that = this
      return new Promise((resolve, reject) => {
        that.$Modal.confirm({
          title: title ? title : '确认',
          content: text,
          onOk: () => {
            return resolve(true)
          },
          onCancel: () => {
            return reject(false)
          }
        })
      })
    };

    //注册全局组件number-input,用来限制输入为数字，并提供报错提示
    Vue.component('number-input', NumInput);
    Vue.component('single-uploader', singleUploader);
    Vue.component('multi-uploader', multiUploader);
    Vue.component('item-select', itemSelectModal);
    Vue.component('multi-item-select', multiItemSelectModal)
  }
}

// 导出组件
export default Common



