import axios from 'axios'
import apis from '../api.js'
import qs from 'qs'
import router from '../router/index.js'
import { getStoreToken, transfer2sha1 } from '../utils.js'
import iView from 'iview';
console.log('process.evn', process.env)
const testAPI1='https://akiviapi.meisaas.com/admin'//正式API
const bdAPI='http://192.168.199.178:8080/admin'
axios.defaults.timeout = 10000; //响应时间
// axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'; //配置请求头
 axios.defaults.baseURL = process.env.baseAPI; //配置接口地址
//axios.defaults.baseURL =bdAPI;
axios.interceptors.request.use((config) => {
  //在发送请求之前做某件事
  if (config.method === 'post' && config.url !== 'http://upload.qiniu.com' && config.url !== 'https://up.qbox.me') {
    let params = config.data;
    let _token = getStoreToken();
    if (_token.merid && _token.token) {
      config.headers['token'] = _token.token;
      params.merid = _token.merid;
    }
    // 把参数，token进行SHA1加密
    let sign = transfer2sha1(params, _token.token);
    params.sign = sign;
    config.data = qs.stringify(params);
  }
  return config;
}, (error) => {
  _.toast("错误的传参", 'fail');
  return Promise.reject(error);
});
axios.interceptors.response.use(res => {
  if (res.data.result != '1' && res.data.code == '100009') {
    iView.Message.error(res.data.msg);
    setTimeout(() => {
      router.push('/login')
    }, 2000)
  }
  return res;
})
export let http = axios;
export let api = apis;



