<template>
  <div>
    <Button type='ghost' v-if='!chosedId' @click='handleShowModal'>选择商品</Button>
    <div v-if='chosedId&&chosedItem.title'>
      <span style="color:#3bc0c3">{{chosedItem.title}}</span>
      <Button @click='handleShowModal' size='small' style='margin-left:20px;'>更换</Button>
      <Button size='small' style='margin-left:10px;' @click='showPreview=true'>详情</Button>
    </div>
    <Modal v-model='showModal' width='800' @on-visible-change='handleHidden'>
      <div class="item-select-main">
      <div style='border-bottom:1px solid silver;padding-bottom:20px;'>
          <span>选择分类：</span>
          <Select v-model='cateid' style="width:200px;margin-left:20px;" placeholder='商品分类' clearable>
            <Option :value='0'>全部</Option>
            <Option v-for="cate in cates" :value="cate.id" :key="cate.id">{{ cate.catename }}</Option>
          </Select>

          <Input placeholder='搜索商品名称' v-model='keyword' style='width:150px;margin:0 20px;' clearable></Input>
          <Button @click='handleSearch'>搜索</Button>

      </div>
      <div class='swiper-item-list'>
        <div v-for='item in items' class="swiper-item-detail" :key='item.id' @click='handleChoseItem(item)' :class='{chosed:tempItem.id==item.id}'>
          <img :src='item.picurl' class='pic'>
          <div class='texts'>
            <div class='texts-title'>{{item.title}}</div>
            <div>价格:￥ {{item.price}}</div>
            <div>库存：{{item.num}}</div>
          </div>
        </div>
        <div style='clear:both'></div>
        <div v-if='!items.length' style='color:#aaa;text-align:center;padding:30px;'>暂无商品</div>
      </div>
      <div class='swiper-item-page'>
        <Page :total="totalcount" :page-size="pagesize" show-total @on-change='indexChange' :current='pageindex+1'></Page>
      </div>
    </div>
    <div slot="footer">
          <Button type='ghost' @click='handleCancel'>取消</Button>
          <Button type='primary' @click='handleOk'>确认</Button>
    </div>
  </Modal>
  <Modal v-model='showPreview'>
    <div slot='header'>商品详情</div>
    <div>
      <div class="swiper-item-detail">
          <img :src='chosedItem.picurl' class='pic'>
          <div class='texts'>
            <div class='texts-title'>{{chosedItem.title}}</div>
            <div>价格:￥ {{chosedItem.price}}</div>
            <div>库存：{{chosedItem.num}}</div>
          </div>
        </div>
        <div style='clear:both'></div>
    </div>
  </Modal>
  </div>
</template>
<script>
export default {
  name: "item-select",
  props: ["value"],
  data() {
    return {
      chosedId: this.value || "",
      cates: [],
      cateid: 0,
      keyword: "",
      totalcount: 0,
      pagesize: 6,
      pageindex: 0,
      items: [],
      chosedItem: {},
      tempItem: "",
      showModal: false,
      showPreview: false
    };
  },
  created() {
    this.getCategoryList();
    this.getItemList();
  },
  methods: {
    handleHidden(value) {
      if (!value) {
        this.tempItem = "";
      }
    },
    handleCancel() {
      this.showModal = false;
    },
    handleOk() {
      let id = this.tempItem.id || "";
      if (!id) {
        this.$Confirm("未选中任何商品，确定要离开选择页面吗", "提示")
          .then(() => {
            this.showModal = false;
          })
          .catch(() => {});
        return;
      }
      this.$Confirm(`你选择了商品：${this.tempItem.title}，请确认选择`, "提示")
        .then(() => {
          this.chosedItem = this.tempItem;
          this.chosedId = this.chosedItem.id;
          this.showModal = false;
        })
        .catch(() => {});
    },
    handleShowModal() {
      this.showModal = true;
    },
    handleInput() {},
    handleSearch() {
      this.pageindex = 0;
      this.getItemList();
    },
    handleChoseItem(item) {
      this.tempItem = item;
    },
    getCategoryList() {
      this.$http
        .post(this.$api.category_list, {})
        .then(res => {
          if (res.data.data) {
            this.cates = res.data.data;
          }
        })
        .catch(erro => {
          console.log("请求cate列表接口", erro);
        });
    },
    getItemList() {
      return this.$http
        .post(this.$api.item_list, this.conditions)
        .then(res => {
          if (res.data.result == "1") {
            this.items = res.data.data;
            this.totalcount = +res.data.totalcount;
          }
        })
        .catch(() => {});
    },
    indexChange(index) {
      this.pageindex = index - 1;
      this.getItemList();
    }
  },
  watch: {
    chosedId(newVal) {
      if (newVal != this.value) {
        this.$emit("input", newVal);
        this.$emit("on-selected", this.chosedItem);
      }
    },
    value(newVal) {
      if (newVal != this.chosedId) {
        this.chosedId = newVal;
        if (!this.chosedItem || this.chosedItem.id != newVal || !newVal) {
          this.chosedItem = "";
          this.$http
            .post(this.$api.item_detail, { itemid: newVal })
            .then(res => {
              if (res.data.result == "1" && res.data.data) {
                this.chosedItem = res.data.data.item;
              } else {
                this.$Message.error(res.data.msg);
                this.chosedItem = "";
                this.chosedId = "";
              }
            })
            .catch(err => {
              this.$Message.error("请求出错");
              this.chosedItem = "";
              this.chosedId = "";
            });
        }
      }
    }
  },
  computed: {
    conditions() {
      return {
        pageindex: this.pageindex || 0,
        pagesize: this.pagesize,
        keywords: this.keyword,
        cateid: this.cateid,
        itemmodel: 1, //普通商品
        ishidden: 0, //商品上架全部状态
        itemtype: 1 //产品类商品
      };
    }
  }
};
</script>
<style>
.swiper-item-page {
  text-align: right;
  padding: 10px;
}

.swiper-item-list {
  height: 400px;
  overflow: auto;
}
.swiper-item-detail {
  border: 1px solid #ddd;
  width: 240px;
  height: 120px;
  border-radius: 8px;
  box-sizing: border-box;
  padding: 10px 10px;
  float: left;
  margin: 30px 5px;
}
.swiper-item-detail:hover,
.swiper-item-detail.chosed {
  background-color: #eee;
  border-color: #3bc0c3;
}
.swiper-item-detail img {
  width: 100px;
  height: 100px;
  display: inline-block;
  vertical-align: top;
}
.swiper-item-detail .texts {
  width: 110px;
  height: 100px;
  display: inline-block;
}
.swiper-item-detail .texts div {
  width: 100%;
  height: 33px;
  line-height: 33px;
  font-size: 14px;
  text-align: center;
}
.swiper-item-detail .texts .texts-title {
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



