<template>
  <div class="select-wrapper">
    <div style="color:#3bc0c3">当前选择的商品：{{chosedTitle}}</div>
    <Button type='ghost' v-if='!chosedItem.length' @click='handleShowModal'>选择商品</Button>
    <div v-else>
      
      <Button @click='handleShowModal' size='small'>更换</Button>
      <Button size='small' style='margin-left:10px;'>详情</Button>
    </div>
    <Modal v-model='showModal' width='800' @on-visible-change='handleHidden'>
      <div class="item-select-main">
      <div style='border-bottom:1px solid silver;padding-bottom:20px;'>
          <span>选择分类：</span>
          <Select v-model='cateid' style="width:200px;margin-left:20px;" placeholder='商品分类' clearable>
            <Option :value='0'>全部</Option>
            <Option v-for="cate in cates" :value="cate.id" :key="cate.id">{{ cate.catename }}</Option>
          </Select>
          <Input placeholder='搜索商品名称' v-model='keyword' style='width:150px;margin:0 20px;' clearable></Input>
          <Button @click='handleSearch'>搜索</Button>
      </div>
      <div class='swiper-item-list'>
        <div v-for='item in items' class="swiper-item-detail" :key='item.id' @click='handleChoseItem(item)' :class='{chosed:hasChosed(item.id)}'>
          <img :src='item.picurl' class='pic'>
          <div class='texts'>
            <div class='texts-title'>{{item.title}}</div>
            <div>价格:￥ {{item.price}}</div>
            <div>库存：{{item.num}}</div>
          </div>
        </div>
        <div style='clear:both'></div>
        <div v-if='!items.length' style='color:#aaa;text-align:center;padding:30px;'>暂无商品</div>
      </div>
      <div class='swiper-item-page'>
        <Page :total="totalcount" :page-size="pagesize" show-total @on-change='indexChange' :current='pageindex+1'></Page>
      </div>
    </div>
    <div slot="footer">
          <div style="text-align:left" >已选择:{{tempItemTitle}}</div>
          <Button type='ghost' @click='handleCancel'>取消</Button>
          <Button type='primary' @click='handleOk'>确认</Button>
    </div>
  </Modal>
  </div>
</template>
<script>
export default {
  props:{
    value:{
      type:Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      cates: [],
      cateid: 0,
      keyword: '',
      totalcount: 0,
      pagesize: 6,
      pageindex: 0,
      items:[],
      chosedItem:[],
      tempItem:[],
      showModal:false,
    }
  },
  created() {
    this.valueChange(this.value)
    this.getCategoryList()
    this.getItemList()
  },
  methods: {
    valueChange(newVal) {
      console.log('新的value',newVal)
      let ids = this.chosedItem.map(it=>it.id);
      let isSame = this.isEqual(ids,newVal);
      if(!isSame){
        let temp = this.getDiff(newVal,this.chosedItem);
        let items = temp.items;
        let missed = temp.missed;
        if(missed.length) {
          Promise.all(missed.map(it=>this.getItemDetail(it))).then(arr=>{
            console.log('获取到的数据',arr)
            this.chosedItem = items.concat(arr)
          }).catch(err=>{
            this.chosedItem = [];
            console.log(err)
            this.$Message.error('获取商品详情失败')
          })
        }else{
          this.chosedItem = items;
        }
      }
    },
    handleHidden(value) {
      if(!value) {
        this.tempItem = [];
      }else {
        this.tempItem = this.chosedItem.map(it=>it)
      }
    },
    handleCancel() {
      this.$Confirm('你的选择将不会保存确定取消吗','提示').then(()=>{
        this.showModal = false;
      }).catch(err=>{

      })
    },
    handleOk() { 
      // if(!this.tempItem.length) {
      //   this.$Confirm('未选中任何商品，确定要离开选择页面吗','提示').then(()=>{
      //     this.showModal = false;
      //   }).catch(()=>{
          
      //   })
      //   return;
      // }
      this.$Confirm(`你选择了商品：${this.tempItemTitle}，请确认选择`,'提示').then(()=>{
        this.chosedItem = this.tempItem.map(it=>it);
        this.showModal = false;
      }).catch(()=>{
      })
    },
    handleShowModal() {
      this.showModal = true;
    },
  	handleInput() {
  	},
  	handleSearch() {
      this.pageindex = 0;
  		this.getItemList()
  	},
  	handleChoseItem(item) {
  		let index = this.tempItem.findIndex(it=>it.id==item.id);
      if(index==-1) {
        this.tempItem.push(item)
      }else {
        this.tempItem.splice(index,1)
      }
  	},
    getCategoryList() {
      this.$http.post(this.$api.category_list, {}).then(res => {
        if (res.data.data) {
          this.cates = res.data.data
        }

      }).catch(erro => {
        console.log('请求cate列表接口', erro)
      })
    },
    getItemList() {
      return this.$http.post(this.$api.item_list, this.conditions).then(res=>{
      	if(res.data.result=='1') {
      		this.items = res.data.data;
      		this.totalcount = +res.data.totalcount
      	}
      }).catch(()=>{})
    },
    indexChange(index) {
      this.pageindex = index - 1;
      this.getItemList();
    },
    isEqual(a,b) {
      if(!Array.isArray(a)||!Array.isArray(b)) {
        return false
      }
      let _a = _.cloneDeep(a);
      let _b = _.cloneDeep(b);
      return _.isEqual(_a.sort(),_b.sort())
    },
    hasChosed(id) {
      return this.tempItem.findIndex(it=>it.id==id)>-1
    },
    getDiff(from,to) {
      let temp = [];
      let missed = []; 
      from.forEach(it=>{
        let filter = to.find(item=>item.id==it)
        if(filter) {
          temp.push(filter)
        }else{
          missed.push(it)
        }
      })
      console.log('missed1111111111111111',missed)
      return {
        items:temp,
        missed
      }
    },
    getItemDetail(itemid) {
      return this.$http.post(this.$api.item_detail, { itemid }).then(res=>{
        if(res.data.result=='1') {
          return res.data.data.item
        }else {
          return Promise.reject(res.data.msg)
        }
      })
    }
  },
  watch:{
  	chosedItem(newVal) {
      console.log('chosedItem',newVal)
  		let ids = newVal.map(it=>it.id);
      let isSame = this.isEqual(ids,this.value);
      if(!isSame) {
        this.$emit('input',ids)
      }
  	},
    value(newVal) {
      this.valueChange(newVal);
    }
  },
  computed:{
  	conditions() {
  		return {
  			pageindex:this.pageindex||0,
  			pagesize:this.pagesize,
  			keywords:this.keyword,
  			cateid:this.cateid,
        itemmodel:1,
  		}
  	},
    tempItemTitle() {
      let res = this.tempItem.reduce((res,val)=>res+','+val.title,'')
      return !res.length?'无':res.slice(1)
    },
    chosedTitle() {
      let res = this.chosedItem.reduce((res,val)=>res+','+val.title,'')
      return !res.length?'无':res.slice(1)
    },
  }
}
</script>
<style scoped>
.swiper-item-page {
  text-align: right;
  padding: 10px;
}

.swiper-item-list {
  height: 400px;
  overflow: auto;
  
}
.select-wrapper {
  margin:10px 0;
  /*border:1px dashed #ccc;*/
  /*padding: 20px;*/
}
.swiper-item-detail {
  border:1px solid #ddd;
  width: 240px;
  height: 120px;
  border-radius:8px;
  box-sizing: border-box;
  padding: 10px 10px;
  float:left;
  margin:30px 5px;
}
.swiper-item-detail:hover,.swiper-item-detail.chosed {
  background-color: #eee;
  border-color: #3bc0c3;
}
.swiper-item-detail img {
  width: 100px;
  height: 100px;
  display: inline-block;
  vertical-align: top;
}
.swiper-item-detail .texts {
  width: 110px;
  height: 100px;
  display: inline-block;

}
.swiper-item-detail .texts div {
  width: 100%;
  height: 33px;
  line-height: 33px;
  font-size:14px;
  text-align: center;
}
.swiper-item-detail .texts .texts-title {
  font-weight: bold;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap; 
}
</style>



