let mainDefault = {
  path: '',
  redirect: 'home'
}

let homeRouter = {
  path: "home",
  component: resolve => {
    require(["../components/Main.vue"], resolve);
  },
  children: [
    //系统管理
    {
      path: "/system/setting",
      name: "Setting",
      component: function(resolve) {
        require(["../components/system/Setting.vue"], resolve);
      }
    },
    {
      path: "/system/text",
      name: "Text",
      component: function(resolve) {
        require(["../components/system/Text.vue"], resolve);
      }
    },
    {
      path: "/system/admin",
      name: "AdminList",
      component: function(resolve) {
        require(["../components/system/AdminList.vue"], resolve);
      }
    },
    {
      path: "/system/company",
      name: "Company",
      component: function(resolve) {
        require(["../components/settings/company.vue"], resolve);
      }
    },
    //店铺
    {
      path: "/store/apply",
      name: "StoreApply",
      component: function(resolve) {
        require(["../components/store/StoreApply.vue"], resolve);
      }
    },
    {
      path: "/store/list",
      name: "StoreList",
      component: function(resolve) {
        require(["../components/store/StoreList.vue"], resolve);
      },
      meta: {
        keepAlive: true
      }
    },
    {
      path: "/store/add",
      name: "StoreAdd",
      component: function(resolve) {
        require(["../components/store/StoreAdd.vue"], resolve);
      }
    },
    {
      path: "/store/detail",
      name: "StoreDetail",
      component: function(resolve) {
        require(["../components/store/StoreDetail.vue"], resolve);
      },
      meta: {
        pname: ["StoreList"]
      }
    },
    //用户管理
    {
      path: "/member/list",
      name: "MemberList",
      component: function(resolve) {
        require(["../components/member/MemberList.vue"], resolve);
      },
      meta: {
        keepAlive: true
      }
    },
    {
      path: "/member/detail",
      name: "MemberDetail",
      component: function(resolve) {
        require(["../components/member/MemberDetail.vue"], resolve);
      },
      meta: {
        pname: ["MemberList", "ApplyReimburse"]
      }
    },
    {
      path: "/proxy/list",
      name: "ProxyList",
      component: function(resolve) {
        require(["../components/member/ProxyList.vue"], resolve);
      },
    
    },
    {
      path: "/proxy/detail",
      name: "ProxyDetail",
      component: function(resolve) {
        require(["../components/member/ProxyDetail.vue"], resolve);
      },
     
    },
    //订单管理
    {
      path: "/order/list",
      name: "OrderList",
      component: function(resolve) {
        require(["../components/order/OrderList.vue"], resolve);
      },
      meta: {
        keepAlive: true
      }
    },
    {
      path: "/order/detail",
      name: "OrderDetail",
      component: function(resolve) {
        require(["../components/order/OrderDetail.vue"], resolve);
      },
      meta: {
        pname: ["OrderList"]
      }
    },
    {
      path: "/repair/list",
      name: "RepairList",
      component: function(resolve) {
        require(["../components/order/RepairList.vue"], resolve);
      }
    },
    {
      path: "/repair/detail",
      name: "RepairDetail",
      component: function(resolve) {
        require(["../components/order/RepairDetail.vue"], resolve);
      }
    },
    //提现管理
    {
      path: "/assets/applyreimburse",
      name: "ApplyReimburse",
      component: function(resolve) {
        require(["../components/assets/ApplyReimburse.vue"], resolve);
      },
      meta: {
        keepAlive: true
      }
    },
    {
      path: "/item/add",
      name: "ItemAdd",
      component: function(resolve) {
        require(["../components/item/AddItem.vue"], resolve);
      }
    },
    {
      path: "/item/edit",
      name: "EditItem",
      component: function(resolve) {
        require(["../components/item/EditItem.vue"], resolve);
      }
    },
    {
      path: "/item/list",
      name: "ItemList",
      component: function(resolve) {
        require(["../components/item/ItemList.vue"], resolve);
      }
    },
    {
      path: "/item/upload",
      name: "ItemUpload",
      component: function(resolve) {
        require(["../components/item/uploadimg.vue"], resolve);
      }
    },
    {
      path: "/swiper/list",
      name: "SwiperList",
      component: function(resolve) {
        require(["../components/swiper/SwiperList.vue"], resolve);
      }
    },
    {
      path: "/swiper/add",
      name: "AddSwiper",
      component: function(resolve) {
        require(["../components/swiper/AddSwiper.vue"], resolve);
      }
    },
    {
      path: "/swiper/edit",
      name: "EditSwiper",
      component: function(resolve) {
        require(["../components/swiper/EditSwiper.vue"], resolve);
      }
    },
    {
      path: "/category/add",
      name: "AddCategory",
      component: function(resolve) {
        require(["../components/category/CategoryAdd.vue"], resolve);
      }
    },
    {
      path: "/category/list",
      name: "CategoryList",
      component: function(resolve) {
        require(["../components/category/Category-List.vue"], resolve);
      }
    },
    {
      path: "/category/edit",
      name: "EditCategory",
      component: function(resolve) {
        require(["../components/category/CategoryEdit.vue"], resolve);
      }
    },
    //活动管理
    {
      path: "/article/add",
      name: "ArticleAdd",
      component: function(resolve) {
        require(["../components/article/AddArticle.vue"], resolve);
      }
    },
    {
      path: "/article/list",
      name: "ArticleList",
      component: function(resolve) {
        require(["../components/article/ArticleList.vue"], resolve);
      }
    },
    {
      path: "/article/edit",
      name: "ArticleEdit",
      component: function(resolve) {
        require(["../components/article/EditArticle.vue"], resolve);
      }
    },
    //内容分类管理
    {
      path: "/articletype/add",
      name: "ArticletypeAdd",
      component: function(resolve) {
        require(["../components/articletype/AddArticleType.vue"], resolve);
      }
    },
    {
      path: "/articletype/list",
      name: "ArticletypeList",
      component: function(resolve) {
        require(["../components/articletype/ArticleTypeList.vue"], resolve);
      }
    },
    {
      path: "/articletype/edit",
      name: "ArticletypeEdit",
      component: function(resolve) {
        require(["../components/articletype/EditArticleType.vue"], resolve);
      }
    },
    //营销活动
    {
      path: "/salesitem/list",
      name: "SalesItemList",
      component: function(resolve) {
        require(["../components/sales/SalesItemList.vue"], resolve);
      }
    },
    {
      path: "/salesitem/add",
      name: "SalesItemAdd",
      component: function(resolve) {
        require(["../components/sales/SalesItemAdd.vue"], resolve);
      }
    },
    //秒杀---------
    {
      path: "/seckilling/list",
      name: "SeckillingList",
      component: resolve =>
        require(["@/components/sales/seckilling/SeckillingList.vue"], resolve)
    },
    {
      path: "/seckilling/add",
      name: "SeckillingAdd",
      component: resolve =>
        require(["@/components/sales/seckilling/SeckillingAdd.vue"], resolve)
    },
    {
      path: "/seckilling/detail",
      name: "SeckillingDetail",
      component: resolve =>
        require(["@/components/sales/seckilling/SeckillingEdit.vue"], resolve)
    },
    //积分---------
    {
      path: "/points/list",
      name: "PointsList",
      component: resolve =>
        require(["@/components/sales/points/PointsList.vue"], resolve)
    },
    {
      path: "/points/add",
      name: "PointsAdd",
      component: resolve =>
        require(["@/components/sales/points/PointsAdd.vue"], resolve)
    },
    {
      path: "/points/detail",
      name: "PointsDetail",
      component: resolve =>
        require(["@/components/sales/points/PointsEdit.vue"], resolve)
    },
    //优惠券
    {
      path: "/coupon/list",
      name: "CouponList",
      component: resolve =>
        require(["@/components/sales/coupon/CouponList.vue"], resolve)
    },
    {
      path: "/coupon/add",
      name: "CouponAdd",
      component: resolve =>
        require(["@/components/sales/coupon/CouponAdd.vue"], resolve)
    },
    {
      path: "/coupon/detail",
      name: "CouponDetail",
      component: resolve =>
        require(["@/components/sales/coupon/CouponEdit.vue"], resolve)
    },
    //微信
    {
      path: "/weixin/wxaconfig",
      name: "WxaConfig",
      component: function(resolve) {
        require(["../components/weixin/WxaConfig.vue"], resolve);
      }
    },
    {
      path: "/weixin/wxapay",
      name: "WxaPayConfig",
      component: function(resolve) {
        require(["../components/weixin/WxaPayConfig.vue"], resolve);
      }
    }
  ]
};

let logsRouter = {
  path: 'logs',
  name: 'logs',
  component: resolve => { require(['../components/logs/Logs.vue'], resolve) }
};

let renewRouter = {
  path: 'renew',
  name: 'renew',
  component: resolve => { require(['../components/renew/Renew.vue'], resolve) }
}

let aboutRouter = {
  path: 'about',
  name: 'about',
  component: resolve => { require(['../components/about/About.vue'], resolve) }
}

export let mainRouters = [mainDefault, homeRouter, logsRouter, renewRouter, aboutRouter];

export let loginRouter = {
  path: '/login',
  name: 'login',
  component: resolve => { require(['../components/login/loginnew.vue'], resolve) }
};

export let registerRouter = {
  path: '/register',
  name: 'register',
  component: resolve => { require(['../components/login/register.vue'], resolve) }
};

export let wxCallback = {
  path: 'wxcallback',
  name: 'WxCallback',
  component: function(resolve) {
    require(['../components/weixin/WxaCallback.vue'], resolve)
  },
  alias: '/weixin/callback'
};



