import Vue from 'vue';
import qs from 'qs';
import Router from 'vue-router'
import {mainRouters,loginRouter,registerRouter,wxCallback} from './routes.js'
import { getStoreToken } from '../utils.js'
import iView from 'iview';
const whiteList = ['/login', '/error', '/wxlogin','/register']
Vue.use(Router)

let router = new Router({
  routes: [
    {
      path:'/',
      component:resolve => { require(['../myapp.vue'], resolve) },
      children:mainRouters
    },
    loginRouter,
    registerRouter,
    wxCallback
  ]
})

router.beforeEach((to, from, next) => {
  iView.LoadingBar.start();
  let token = getStoreToken().token;
  from.meta.keepAlive=false;
  if(!!from.meta.pname){
    //   console.log(from.meta.pname);
     let pname=from.meta.pname.find(name=>name==to.name);
     if(!!pname){
        to.meta.keepAlive=true;
     }   
  }
  if (!token && whiteList.indexOf(to.path) == -1) {
     next('/login')
  }else if(to.path=='/home') {
    let search = location.search
    if(search&&search.indexOf('auth_code')>-1) {
      next('/weixin/callback')
    }else{
      next('/member/list')
    }
    
  }else{
    return next();
  }
  
});
router.afterEach(route => {
  iView.LoadingBar.finish();
});

export default router



