<template>
  <div>
    <vue2-editor v-model="htmlStr" useCustomImageHandler @imageAdded="handleImageAdded" :id='id' :editorToolbar="customToolbar" :disabled='!!disabled'></vue2-editor>
  </div>
</template>
<script>
import { VueEditor } from 'vue2-editor'
import axios from 'axios'
import {defaultBar,fullBar} from './toolBarOpt.js'
export default {
  name: 'Vue2Editor',
  props: ['content','disabled'],
  data() {
    return {
      htmlStr: this.content,
      uploaderUrl: this.$api.qiniu_upload,
      imgDomain: 'http://akivipic.meisaas.com/',
      id: this.mockKey(),
      customToolbar:defaultBar
    }
  },
  components: {
    'vue2-editor': VueEditor
  },
  watch: {
    htmlStr(newval) {
      this.$emit('change', newval)
    },
    content(newval) {
      this.htmlStr = newval
    }
  },
  methods: {
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      let formData = new FormData();
      let type = file.name.split('.');
      if (type.length < 2) {
        return false
      }
      type = type.pop();
      this.getToken(type).then(res => {
        formData.append('file', file);
        formData.append('key', res.key);
        formData.append('token', res.token);
        this.$http({
          method: 'post',
          data: formData,
          headers: {
            'Content-Type': 'multipart/form-data'
          },
          url: this.uploaderUrl
        }).then(result => {
          let url = this.imgDomain + result.data.key // Get url from response
          Editor.insertEmbed(cursorLocation, 'image', url);
          resetUploader()
        }).catch(erro => {
          console.log(erro)
        })
      })

    },
    getToken(type) {
      return this.$http.post('/qiniu/token', {}).then(res => {
        return {
          token: res.data.data.token,
          key: this.mockKey() + '.' + type
        }
      }).catch(erro => {
        return Promise.reject(false)
      })
    },
    mockKey() {
      let rdNum = ('' + Date.now()).slice(-8);
      let str = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz_';
      let len = str.length;
      let res = '';
      for (let i = 0; i < 8; i++) {
        res += str[Math.floor(Math.random() * len)];
      }
      return res + rdNum;
    },
  },
}

</script>
<style>


</style>



