// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './new-app'
import router from './router/index.js'
import iView from 'iview';
import 'iview/dist/styles/iview.css';
import VueHtml5Editor from 'vue-html5-editor'
import './theme/lormi.less';
import { http, ajax, api } from './request/index.js'
import { getStoreToken } from './utils.js'

import editorConfig from './editor/vue-html5-editor.js'
import Vue2Editor from './editor/vue2editor.js'
import myConfirm from './components/common/Confirm.js'
import sha1 from 'sha1'
Vue.config.productionTip = false
Vue.prototype.$sha1 = sha1
Vue.use(iView);
Vue.use(Vue2Editor);
var sitemap = {
  prvname: '',
  currname: ''
}
Vue.prototype.$sitemap = sitemap;
Vue.prototype.$api = api
var menuActive = {
  open: [],
  active: 0
}
Vue.prototype.$menuactive = menuActive;

Vue.prototype.$http = http;

Vue.use(VueHtml5Editor, editorConfig);

Vue.use(myConfirm)

/* eslint-disable no-new */


new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
});



