import sha1 from 'sha1'
import qs from 'qs'
import { Base64 } from 'js-base64'
export function storeToken(data) {
  localStorage.setItem('merid', data.merid)
  localStorage.setItem('token', data.token)
}

export function getStoreToken() {
  return {
    merid: localStorage.getItem('merid'),
    token: localStorage.getItem('token')
  }
}

export function removeToken() {
  localStorage.removeItem('merid')
  localStorage.removeItem('token')
}

export function mockKey() {
  let rdNum = ('' + Date.now()).slice(-8);
  let str = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxy_';
  let len = str.length;
  let res = '';
  for (let i = 0; i < 8; i++) {
    res += str[Math.floor(Math.random() * len)];
  }
  return res + rdNum;
}

export function transfer2sha1(data, token) {
  let signescape = data.signescape;
  let tempkey = {};
  if (signescape) {
    signescape.forEach(key => {
      tempkey[key] = Base64.encode(data[key])
    });
    data.signescape = JSON.stringify(tempkey)
  }

  let origin = Object.assign({}, data, { token });
  // console.log('origin',origin)
  let arr = Object.keys(origin).sort();
  let temp = arr.reduce((res, val) => {
    if (val == 'sign' || val == 'signescape') {
      return res;
    };
    let obj = {};
    if (signescape && signescape.indexOf(val) > -1) {
      obj[val] = tempkey[val]
    } else {
      obj[val] = origin[val];
    }
    return res + '&' + qs.stringify(obj, { format: 'RFC1738' });
  }, '').slice(1);
  return sha1(temp)
}

export function formatCascader(data) {
  let map = {
    first_class: [],
    second_class: [],
    third_class: []
  };
  let arr = []
  for (let i = 0; i < data.length; i++) {
    let tempMap = {};
    tempMap.value = data[i].first_id;
    tempMap.label = data[i].first_class;
    map.first_class.push(tempMap);
    let first_obj = {};
    arr.push(first_obj)
    first_obj.value = data[i].first_id;
    first_obj.label = data[i].first_class;
    first_obj.children = [];
    let datasec = data[i].second_list
    for (let j = 0; j < datasec.length; j++) {
      let tempSec = {};
      tempSec.value = datasec[j].second_id;
      tempSec.label = datasec[j].second_class;
      map.second_class.push(tempSec);
      let second_obj = {};
      first_obj.children.push(second_obj);
      second_obj.value = datasec[j].second_id;
      second_obj.label = datasec[j].second_class;
      if (datasec[j].third_list) {
        second_obj.children = [];
        let datathr = datasec[j].third_list;
        for (let k = 0; k < datathr.length; k++) {
          let tempThr = {};
          tempThr.value = datathr[k].third_id;
          tempThr.label = datathr[k].third_class;
          map.third_class.push(tempThr);
          let third_obj = {};
          second_obj.children.push(third_obj);
          third_obj.value = datathr[k].third_id;
          third_obj.label = datathr[k].third_class;
        }
      }
    }
  };
  return { map, tree: arr }
}



