<template>
  <div>
    <div class="upload-list" v-for="item in images">
        <img :src="item.picurl">
        <div class="action-cover">
          <div class="action-cover-icons">
            <Icon type="ios-eye-outline" @click.native="handleView(item.picurl)" title='查看大图'></Icon>
            <Icon type="ios-trash-outline" @click.native="handleRemove(item)" title='删除图片'></Icon>
            <Icon type="ios-refresh-outline" @click.native="handleChangePic(item)" title='更换图片'></Icon>
          </div>
        </div>
    </div>
    <div v-for='item in uploadList' class="upload-list">
       <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
    </div>
    <div class="show-area" @click='handleAddNewPic' v-if='!isMax'>
      <Icon type="plus" size="30"></Icon>
    </div>
    <div class="max-hint" v-else>
      不能超过{{max}}张
    </div>
    <Upload v-show='false' ref="upload" :show-upload-list="false" :on-success="handleSuccess" :format="format" :max-size="4096" :on-format-error="handleFormatError" :on-exceeded-size="handleMaxSize" :before-upload="handleBeforeUpload" :action="uploadURL" :data='qiniuData'>
      <span ref='trigger' v-show='false' tabindex="-1"></span>
    </Upload>
    <Modal title="预览" v-model="visible">
      <img :src='viewSrc' v-if="viewSrc" style="width: 100%">
    </Modal>
  </div>
</template>
<script>

export default {
  props: {
    images:{
      type:Array,
      default() {
        return [];
      },
    },
    format:{
      type:Array,
      default() {
        return [];
      },
    },
    max:{
      type:Number,
      default:5,
    },
  },
  data() {
    return {
      changeId:'',
      uploadList: [],
      viewSrc: '',
      visible: false,
      uploadURL:this.$api.qiniu_upload,
      qiniuData: {
        token: '',
        key: '',
      },
      bucket: 'http://akivipic.meisaas.com/'
    }
  },
  computed:{
    isMax() {
      return this.images.length>=this.max
    }
  },
  methods: {
    handleAddNewPic() {
      this.changeId = '';
      this.$refs.trigger.click()
    },
    handleChangePic(file) {
      this.confirm('上传一张新图片替换原图片').then(res => {
        this.changeId = file.id
        this.$refs.trigger.click()
      }).catch(erro => {})
    },
    handleSuccess(res, file, fileList) {
      file.url = this.bucket + res.key
      fileList.splice(fileList.length-1,1)
      if(this.changeId) {
        this.$emit('on-change',{picurl:file.url,id:this.changeId})
        this.changeId = ''
      }else {
        this.$emit('on-add', file.url)
      }

    },
    handleFormatError() {},
    handleMaxSize(file) {
      this.$Notice.warning({
        title: '图片大小超出限制',
        desc: '文件 ' + file.name + ' 太大, 不能超过4M.'
      });
    },
    handleBeforeUpload(file, fileList) {
      let type = file.name.split('.')
      if(type.length<2){
        return false
      }
      type = type.pop()
      return this.getToken(type)
    },
    getToken(type) {
      return this.$http.post('/qiniu/token', {}).then(res => {
        this.qiniuData.token = res.data.data.token
        this.qiniuData.key = this.mockKey() + '.' + type
      }).catch(erro => {
        return Promise.reject(false)
      })
    },
    handleView(src) {
      this.viewSrc = src;
      this.visible = true;
    },
    handleRemove(file) {
      this.confirm('图片将永久删除，请确认！', '警告').then(res => {
        this.$emit('on-del', file.id)
      }).catch(erro => {})

    },
    confirm(text, title) {
      let that = this
      return new Promise((resolve, reject) => {
        that.$Modal.confirm({
          title: title ? title : '确认',
          content: text,
          onOk: () => {
            return resolve(true)
          },
          onCancel: () => {
            return reject(false)
          }
        })
      })
    },
    mockKey() {
      let rdNum = ('' + Date.now()).slice(-8);
      let str = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz_';
      let len = str.length;
      let res = '';
      for (let i = 0; i < 8; i++) {
        res += str[Math.floor(Math.random() * len)];
      }
      return res + rdNum;
    },
  },
  mounted() {
    this.uploadList = this.$refs.upload.fileList;
  },
  watch: {

  }
}

</script>
<style scoped>
.max-hint {
  display: inline-block;
  margin-left: 75px;
  color: #ccc;
}

.action-cover {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, .6);
}

.show-area {
  width: 75px;
  height: 75px;
  color: #ccc;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  line-height: 75px;
  border: 1px dashed;
  border-radius: 5px;
  cursor: pointer;
}

.show-area:hover {

  color: #3bc0c3;
}

.upload-list {
  display: inline-block;
  width: 75px;
  height: 75px;
  text-align: center;
  line-height: 75px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
  margin-right: 4px;
}

.upload-list img {
  width: 100%;
  height: 100%;
}

.upload-list:hover .action-cover {
  display: block;
}

.action-cover i {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin: 0 2px;
}

</style>



